
import { defineComponent, onMounted } from "vue";
import { setCurrentPageTitle } from "@/core/helpers/breadcrumb";
import { reactive } from "vue";
import ApiService from "@/core/services/ApiService";
import Swal from "sweetalert2/dist/sweetalert2.min.js";

export default defineComponent({
  name: "cases",
  components: {},
  setup() {
    const state = reactive({
      count: 0,
      mounted: false,
      projects: null,
    });

    let apiUrl = "http://localhost:5000";

    return {
      state,
      apiUrl,
    };
  },
  mounted() {
    console.log(location.host);
    if (location.host.indexOf("localhost") > -1) {
      this.apiUrl = "http://localhost:5000";
    } else {
      this.apiUrl = "";
    }
    setCurrentPageTitle("Projects");
    this.state.mounted = true;
    this.getProjects();
  },
  methods: {
    showProject(id) {
      this.$router.push("/project/" + id + "/studio");
    },
    getProjects() {
      console.log(this.apiUrl + "get");
      ApiService.post(this.apiUrl + "/api/projects", {})
        .then(({ data }) => {
          this.state.projects = data.filter(x => x.id != -1);
          console.log("get projects", data);
        
        })
        .catch(({ response }) => { });
    },
    addProject() {
      console.log("add");
      ApiService.post(this.apiUrl + "/api/project/add", {})
        .then(({ data }) => {
          //this.state.projects = data;
          this.getProjects();
        })
        .catch(({ response }) => { });
    },
    saveProject(id) {
      console.log("save");
      ApiService.post(this.apiUrl + "/api/project/" + id.toString(), {})
        .then(({ data }) => {
          //this.state.projects = data;
          // this.getProjects();
        })
        .catch(({ response }) => { });
    },
    deleteProject(id) {
      console.log("delete");
      ApiService.delete(this.apiUrl + "/api/project/" + id.toString())
        .then(({ data }) => {
          //this.state.projects = data;
          // this.getProjects();
            Swal.fire({
            text: "Project deleted successfully.",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok, got it!",
            customClass: {
              confirmButton: "btn btn-primary",
            },
          });
        })
        .catch(({ response }) => { });
    },
  },
});
